.shop {
  position: fixed;
  top: 50%;
  left: 50%;
  width: 300px;
  transform: translate(-50%, -50%);
  background-color: rgb(45, 19, 2);
  padding: 30px;
  border-radius: 15px;
  z-index: 10;
  box-shadow: 0 0 20px rgba(0,0,0,0.3);
  color: white;
  border: 5px solid rgb(9, 2, 0);
  display: flex;
  flex-direction: column;
  row-gap: 5px;
  text-align: right;
}

.shop button {
    background-color: rgb(45, 19, 2);
    color: white;
    border: 2px solid white;
    padding: 10px 15px;
    cursor: pointer;
}

.shop h2 {
    margin: auto;
    margin-bottom: 20px;
}

.boughtUpgrade {
  background-color: rgb(75, 45, 23) !important;
}
